.logo-side.left {
  --direction: -1;
}

.logo-side.right {
  --direction: 1;
}

.logo-side {
  width: 2px;
  height: 1.7rem;
  background: #059bf2;
  -webkit-transition: -webkit-transform 0.3s ease-in-out;
  transition: -webkit-transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
  --translate: 1.4rem;
  -webkit-transform: translateX(calc(var(--direction) * var(--translate))) scale(0);
          transform: translateX(calc(var(--direction) * var(--translate))) scale(0);
  -webkit-transform-origin: center;
          transform-origin: center;
}

.logo-side:before,
.logo-side:after {
  content: "";
  width: 2px;
  height: 1.7rem;
  background: #059bf2;
  position: absolute;
  --angle: 60deg;
  -webkit-transition: -webkit-transform 0.2s ease-in-out;
  transition: -webkit-transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}

.logo-side:before {
  top: -100%;
  -webkit-transform-origin: bottom;
          transform-origin: bottom;
}

.logo-side:after {
  bottom: -100%;
  -webkit-transform-origin: top;
          transform-origin: top;
}

.logo-side.left:before,
.logo-side.right:after {
  --direction: 1;
}

.logo-side.right:before,
.logo-side.left:after {
  --direction: -1;
}

.logo-side:before,
.logo-side:after {
  -webkit-transform: translateX(-50%) rotate(calc(var(--direction) * var(--angle)))
    scale(0);
          transform: translateX(-50%) rotate(calc(var(--direction) * var(--angle)))
    scale(0);
}

@media (hover: hover) {
  .logo-btn:hover .logo-side {
    -webkit-transform: translateX(calc(var(--direction) * var(--translate))) scale(1);
            transform: translateX(calc(var(--direction) * var(--translate))) scale(1);
  }

  .logo-btn:hover .logo-side:before,
  .logo-btn:hover .logo-side:after {
    -webkit-transition: -webkit-transform 0.2s ease-in-out 0.3s;
    transition: -webkit-transform 0.2s ease-in-out 0.3s;
    transition: transform 0.2s ease-in-out 0.3s;
    transition: transform 0.2s ease-in-out 0.3s, -webkit-transform 0.2s ease-in-out 0.3s;
  }

  .logo-btn:hover .logo-side:before,
  .logo-btn:hover .logo-side:after {
    -webkit-transform: translateX(-50%) rotate(calc(var(--direction) * var(--angle)))
      scale(1);
            transform: translateX(-50%) rotate(calc(var(--direction) * var(--angle)))
      scale(1);
  }

  .navbar-link:hover:after {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@media (hover: none) {
  .logo-btn .logo-side {
    -webkit-transform: translateX(calc(var(--direction) * var(--translate))) scale(1);
            transform: translateX(calc(var(--direction) * var(--translate))) scale(1);
  }

  .logo-side:before,
  .logo-side:after {
    -webkit-transform: translateX(-50%) rotate(calc(var(--direction) * var(--angle)))
      scale(1);
            transform: translateX(-50%) rotate(calc(var(--direction) * var(--angle)))
      scale(1);
  }
}

