  z-index: 10;
  position: absolute;
  -webkit-transition: opacity 500ms ease-in-out;
  transition: opacity 500ms ease-in-out;
}

.ReactModal__Overlay--after-open {
  opacity: 1;
}

.ReactModal__Overlay--before-close {
  opacity: 0;
}

.modal-header {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid black;
}

.modal-header h1 {
  font-size: 40px;
  font-weight: 600;
  margin-bottom: 1rem;
}

.modal-close-btn {
  cursor: pointer;
  border: none;
  outline: none;
  background: none;
  font-size: 30px;
  font-weight: bold;
  position: absolute;
  top: 5%;
  right: 5%;
}

.project-info {
  display: flex;
  align-items: center;
}

.tag-container {
  width: 100%;
  padding-bottom: 1%;
  display: flex;
  flex-wrap: wrap;
}

.tag-container div {
  box-sizing: content-box;
  border: 2px solid red;
  border-radius: 5px;
  padding: 0.3rem;
  margin: 0;
  margin-right: 2%;
  text-align: center;
}

.modal-btn-container {
  display: flex;
  align-items: center;
  padding: 2.5rem;
  padding-top: 0;
}

.modal-btn {
  width: 7vw;
  min-width: 50px;
  max-width: 75px;
  height: 7vw;
  min-height: 50px;
  max-height: 75px;
  border: none;
  outline: none;
  border-radius: 50%;
  position: relative;
}

.modal-btn:first-of-type {
  margin-right: 3rem;
}

.modal-btn.unavailable {
  background: #e05858;
}

.modal-btn a {
  width: 100%;
  height: 100%;
  text-decoration: none;
}

.modal-btn i {
  color: #5aa1f2;
  font-size: 3vw;
}

.modal-btn.unavailable i,
.modal-btn.unavailable .btn-caption {
  color: #b5b5b5;
}

@media (max-width: 700px) {
  .modal-btn i {
    font-size: 20px;
  }
}

@media (min-width: 1070px) {
  .modal-btn i {
    font-size: 35px;
  }
}

.btn-caption {
  pointer-events: none;
  color: black;
  font-weight: 600;
  position: absolute;
  width: 200%;
  bottom: 0;
  left: 50%;
  -webkit-transform: translate(-50%, 100%);
          transform: translate(-50%, 100%);
  opacity: 0;
  -webkit-transition: opacity 0.2s ease-in-out;
  transition: opacity 0.2s ease-in-out;
}

@media (hover: hover) {
  .modal-btn:hover .btn-caption {
    opacity: 1;
  }
}

@media (hover: none) {
  .btn-caption {
    opacity: 1;
  }
}

@media (max-width: 420px) {
  .project-info {
    flex-direction: column;
    align-items: flex-start;
  }
  .modal-btn-container {
    order: 0;
    width: 100%;
    justify-content: space-around;
    padding: 0;
    padding-bottom: 2.5rem;
  }
  .tag-container {
    order: 1;
  }
}

.modal-body {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;
}

.modal-body a {
  color: blue;
}

.modal-body h4 {
  margin-bottom: 0;
  font-weight: 600;
}

.modal-body h5 {
  padding-left: 1rem;
  margin-top: 1rem;
  font-weight: 600;
}

.modal-body-description {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.modal-body-description p {
  font-size: 18px;
  margin: 2rem 3rem;
}

.modal-body-description img {
  width: 500px;
}

@media (max-width: 700px) {
  .modal-body-description p {
    font-size: 18px;
    margin: 2rem 0;
  }

  .modal-body-description img {
    width: 100%;
  }
}

